button, .button {
    padding:13px 30px;
    background:$purple;
    border:none;
    color:$white;
    margin:15px 0;
    font-size:16px;
    display:inline-block;
    text-decoration:none;
    font-weight:600;
    font-family:"Open Sans";
    cursor: pointer;
    text-align: center;
    position: relative;
    z-index: 1;
    color: #fff;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    -webkit-transition:box-shadow 0.2s;
    transition:box-shadow 0.2s;

}
button:hover, .button:hover {
    box-shadow:0 0 0 4px $purple,
        inset 0 0 0 3px $white;
    color:#fff;

}
button:active, .button:active {}
button.reversed, .button.reversed {
    -webkit-transition:box-shadow 0.2s;
    transition:box-shadow 0.2s;
    background:$white;
    color:$purple;

}
button.reversed:hover, .button.reversed:hover {
    box-shadow:0 0 0 4px $white,
         inset 0 0 0 3px $purple;
    color:$purple;

}
button.reversed:active, .button.reversed:active {}
